<template>
  <div v-if="selectComp" class="order-page">
    <vxe-modal v-model="typeVisible" title="" width="480" destroy-on-close>
      <template v-slot>
        <div class="pro-pays">
          <vxe-radio-group v-model="payType">
          <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26"></vxe-radio>
          <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26"></vxe-radio>
        </vxe-radio-group>
        </div>
        <div class="pro-btns">
          <vxe-button status="primary" :disabled="!payType" @click="pay()"></vxe-button>
        </div>
      </template>
    </vxe-modal>

    <vxe-modal v-model="qqVisible" title="" height="200" width="480" destroy-on-close>
      <template v-slot>
        <vxe-form ref="qqForm" :data="qqFormData" :rules="qqFormRules" @submit="vaildQQ" title-width="90" title-align="right" title-colon>
          <vxe-form-item title="">
            <template v-slot>
              <vxe-radio-group v-model="payType">
                <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26"></vxe-radio>
                <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26"></vxe-radio>
              </vxe-radio-group>
            </template>
          </vxe-form-item>
          <vxe-form-item field="qq" title="QQ">
            <template v-slot="scope">
              <vxe-input type="integer" ref="email" v-model="qqFormData.qq" :controls="false" placeholder="QQ" clearable @input="$refs.qqForm.updateStatus(scope)"></vxe-input>
              <vxe-button status="primary" type="submit" :disabled="!qqFormData.qq"></vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>

    <vxe-modal v-model="emailVisible" title="" height="300" width="480" destroy-on-close>
      <template v-slot>
        <vxe-form ref="emailForm" :data="emailFormData" :rules="emailFormRules" @submit="vaildEmail" title-width="90" title-align="right" title-colon>
          <vxe-form-item title="">
            <template v-slot>
              <vxe-radio-group v-model="payType">
                <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26"></vxe-radio>
                <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26"></vxe-radio>
              </vxe-radio-group>
            </template>
          </vxe-form-item>
          <vxe-form-item field="qq" title="QQ">
            <template v-slot="scope">
              <vxe-input ref="qq" v-model="emailFormData.qq" placeholder="QQ" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
            </template>
          </vxe-form-item>
          <vxe-form-item field="email" title="">
            <template v-slot="scope">
              <vxe-input ref="email" v-model="emailFormData.email" placeholder="" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
              <vxe-button @click="getEmail" :disabled="triggerEmail || emailDownTime > 0">{{ checkEmail ? '' : (emailDownTime > 0 ? emailDownTime + '' : '') }}</vxe-button>
            </template>
          </vxe-form-item>
          <vxe-form-item field="code" title="" :item-render="{autofocus: '.vxe-input--inner'}">
            <template v-slot="scope">
              <vxe-input v-model="emailFormData.code" placeholder="" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
              <vxe-button status="primary" type="submit" :disabled="!emailFormData.email"></vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>

    <vxe-modal v-model="payVisible" :loading="payLoading" :show-header="false" width="500" height="480">
      <template v-slot>
        <div class="pay-popup" v-if="selectPay">
          <div class="pay-desc">
            <template v-if="payType === 'zfb'">
              <img class="pay-icon" src="../assets/image/support/alipay_icon.png">
            </template>
            <template v-else-if="payType === 'wx'">
              <img class="pay-icon" src="../assets/image/support/wechat_icon.png">
            </template>
            <span>{{ $utils.getPayType(payType) }}</span>
          </div>
          <div class="pay-qr">
            <div class="pay-left">
              <img :src="qrUrl">
            </div>
            <div class="pay-right">
              <div class="pay-price">
                <span class="price-unit">{{ selectPay.amount }}</span>
              </div>
              <div class="pay-down">
                <div class="pay-msg"><br></div>
                <div class="pay-time">{{ payCountdown }}</div>
              </div>
              <div class="pay-oper">
                <vxe-button status="info" @click="cancelPay" size="mini"></vxe-button>
              </div>
            </div>
          </div>
          <div class="pay-name">
            <span class="bold"></span>
            <span>{{ selectPay.appCode }}</span>
          </div>
          <div class="pay-no">
            <span class="bold"></span>
            <span>{{ selectPay.orderNo }}</span>
          </div>
          <div class="pay-type">
            <span class="bold"></span>
            <span>{{ $utils.getPayType(payType) }}</span>
          </div>
          <div class="pay-name">
            <span class="bold"></span>
            <span>{{ selectPay.email || '-' }}</span>
          </div>
          <div class="pay-name">
            <span class="bold">Q Q </span>
            <span>{{ selectPay.qq || '-' }}</span>
          </div>
        </div>
      </template>
    </vxe-modal>

    <vxe-modal v-model="proVisible" :loading="proLoading" :title="`${selectComp.name} - `" width="500" height="440" esc-closable>
      <template v-slot>
        <div v-if="proPrompt">
          <div class="pro-prompt">
            <div> <qq-service></qq-service> </div>
            <div class="pp-btns">
              <vxe-button status="primary" content="" @click="proPrompt = false"></vxe-button>
            </div>
          </div>
        </div>
        <div v-else>
          <div class="pro-pays">
            <vxe-radio-group v-model="payType">
              <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26"></vxe-radio>
              <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26"></vxe-radio>
            </vxe-radio-group>
          </div>
          <div class="pro-amoun">
            <div class="comp-amoun">{{ selectComp.amount }}</div>
          </div>
          <div class="pro-qr" :style="{visibility: proLoading ? 'hidden' : 'visible'}">
            <img :src="`${apiURL}/api/qr/${code}_${payType}.png`" height="200">
          </div>
          <div class="pro-desc">
            <span class="red"> <qq-service></qq-service> </span>
          </div>
        </div>
      </template>
    </vxe-modal>

    <vxe-modal v-model="supportVisible" title=" vxe-table " width="440" height="300" esc-closable>
      <template v-slot>
        <div style="text-align: center;">
          <div>
            <img src="../assets/image/support/qq.png" style="width: 189px; height: 183px">
          </div>
          <div>QQ652309891</div>
        </div>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'
import QRCode from 'qrcode'

const winAlert = alert

export default {
  name: 'CompOrder',
  props: {
    code: String
  },
  data () {
    return {
      proPrompt: true,
      proVisible: false,
      proLoading: false,
      payType: 'zfb',

      emailVisible: false,
      checkEmail: true,
      triggerEmail: false,
      emailFormData: {
        email: '',
        qq: '',
        code: ''
      },
      emailFormRules: {
        email: [
          { required: true, pattern: /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/, message: '' }
        ],
        code: [
          { required: true, message: '' }
        ]
      },

      qqVisible: false,
      qqFormData: {
        qq: ''
      },
      qqFormRules: {
        qq: [
          { required: true, pattern: /^[1-9][0-9]{4,12}$/, message: 'QQ' }
        ]
      },

      supportVisible: false,
      typeVisible: false,

      payVisible: false,
      payLoading: false,
      selectPay: null,
      qrUrl: '',
      payCountdown: '',
      emailDownTime: 0
    }
  },
  computed: {
    ...mapState([
      'apiURL',
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  destroyed () {
    clearTimeout(this.edTimeout)
    this.proVisible = false
    this.payVisible = false
  },
  methods: {
    ...mapMutations([
      'updateLoginInfo'
    ]),
    open () {
      clearTimeout(this.edTimeout)
      this.proPrompt = true
      this.checkEmail = true
      this.triggerEmail = false
      this.qqVisible = false
      this.proVisible = false
      this.supportVisible = false
      this.typeVisible = false
      this.emailDownTime = 0
      if (this.selectComp.pays && this.selectComp.pays.length) {
        if (!this.selectComp.pays.includes(this.payType)) {
          this.payType = this.selectComp.pays[0]
        }
      } else {
        this.payType = ''
      }
      XEUtils.clear(this.emailFormData, '')
      this.updateLoginInfo()
      if (['SupportQQ'].includes(this.code)) {
        this.qqVisible = true
      } else if (['PRO'].includes(this.code)) {
        this.proLoading = true
        this.proVisible = true
        setTimeout(() => {
          this.proLoading = false
        }, 300)
      } else {
        this.proVisible = false
        if (this.loginToken && this.loginInfo) {
          this.typeVisible = true
        } else {
          this.$XModal.confirm({
            escClosable: true,
            message: '',
            title: '',
            cancelButtonText: '',
            confirmButtonText: ''
          }).then(type => {
            if (type === 'confirm') {
              document.getElementById('login').click()
            } else if (type === 'cancel') {
              this.emailVisible = true
            }
          })
        }
      }
    },
    startDownTime () {
      if (this.emailVisible) {
        this.edTimeout = setTimeout(() => {
          this.emailDownTime -= 1
          if (this.emailDownTime > 0) {
            this.startDownTime()
          }
        }, 1000)
      }
    },
    getEmail () {
      if (!this.emailFormData.email) {
        this.$refs.email.focus()
        return
      }
      if (this.emailDownTime <= 0) {
        this.checkEmail = true
        this.triggerEmail = true
        XEAjax.post('/api/pub/vxetable/check/email', {
          email: this.emailFormData.email
        }).then(data => {
          const rest = this.$utils.getRest(data)
          if (rest && rest.result && rest.result.email) {
            this.$XModal.message({
              message: '',
              status: 'success'
            })
            this.checkEmail = false
            this.triggerEmail = false
            this.emailFormData.code = ''
            this.emailDownTime = rest.result.ss * 60
            this.startDownTime()
          } else {
            this.triggerEmail = false
            if (rest.result && rest.result.downTime) {
              this.checkEmail = false
              this.emailDownTime = rest.result.downTime
              this.startDownTime()
            }
            this.$XModal.message({
              message: rest.message,
              status: 'error'
            })
          }
        })
      }
    },
    vaildEmail () {
      if (!this.emailFormData.email) {
        this.$XModal.message({
          message: '',
          status: 'error'
        })
        return
      }
      if (!this.emailFormData.code) {
        this.$XModal.message({
          message: '',
          status: 'error'
        })
        return
      }
      XEAjax.post('/api/pub/vxetable/vaild/email', this.emailFormData).then(data => {
        const rest = this.$utils.getRest(data)
        if (rest && rest.result && rest.result.email) {
          this.checkEmail = false
          this.pay({ email: rest.result.email })
        } else {
          this.$XModal.message({
            message: rest.message,
            status: 'error'
          })
        }
      })
    },
    vaildQQ () {
      if (!this.qqFormData.qq) {
        this.$XModal.message({
          message: 'QQ',
          status: 'error'
        })
        return
      }
      this.pay({ qq: this.qqFormData.qq })
    },
    cancelPay () {
      this.$XModal.confirm({
        message: '',
        title: ''
      }).then(type => {
        if (type === 'confirm') {
          this.payVisible = false
          this.updateLoginInfo()
        }
      })
    },
    pay ({ email, qq } = {}) {
      this.qqVisible = false
      this.emailVisible = false
      this.payVisible = true
      this.payLoading = true
      this.typeVisible = false
      this.qrUrl = ''
      this.payCountdown = ''
      this.selectPay = null
      XEAjax.post(`/api/auth/pay/${this.payType}/create/${this.code}`, {
        qq,
        email
      }).then(data => {
        const rest = this.$utils.getRest(data)
        this.payLoading = false
        if (rest && rest.result) {
          this.selectPay = rest.result
          this.getAlipayStatus().then((rest) => {
            this.payVisible = false
            this.updateLoginInfo()
            if (rest && rest.status === 'success') {
              if (this.code === 'SupportQQ') {
                this.supportVisible = true
              } else {
                this.$XModal.alert({
                  message: '',
                  title: '',
                  status: 'success'
                })
              }
            } else if (rest && rest.status === 'cancel') {
            } else {
              this.$XModal.alert({
                message: '',
                title: '',
                status: 'warning'
              })
            }
          }).catch(() => {
            this.payVisible = false
            this.updateLoginInfo()
            this.$XModal.alert({
              message: '7',
              title: '',
              status: 'error'
            })
          })
          QRCode.toDataURL(rest.result.qrCode, { width: 200 }).then(url => {
            this.qrUrl = url
          })
        } else {
          winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
        }
      }).catch(() => {
        this.payLoading = false
        winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
      })
    },
    getAlipayStatus () {
      let isOut = false
      const orderNo = this.selectPay.orderNo
      const timeout = XEUtils.toNumber(this.selectPay.timeout)
      const endDate = new Date(Date.now() + Math.max(1000, timeout - 1000))
      const outPromise = new Promise(resolve => {
        const updateDowntime = () => {
          if (this.payVisible) {
            const dateDiff = XEUtils.getDateDiff(new Date(), endDate)
            isOut = !dateDiff.done
            if (isOut) {
              resolve({
                status: 'timeout'
              })
            } else {
              this.payCountdown = `${dateDiff.mm}${dateDiff.ss}`
              this.downTime = setTimeout(() => {
                if (this.payVisible) {
                  updateDowntime()
                }
              }, 1000)
            }
          }
        }
        updateDowntime()
      })
      return Promise.race([
        outPromise,
        new Promise((resolve, reject) => {
          const queryResult = () => {
            if (this.payVisible) {
              if (isOut) {
                resolve({
                  status: 'timeout'
                })
              } else {
                setTimeout(() => {
                  if (this.payVisible) {
                    XEAjax.get(`/api/auth/pay/query/${orderNo}`).then(data => {
                      const rest = this.$utils.getRest(data)
                      if (rest.result.status === 'success') {
                        resolve(rest.result)
                      } else if (rest.result.status === 'wait') {
                        queryResult()
                      } else {
                        resolve(rest.result)
                      }
                    }).catch(() => {
                      const rest = {
                        status: 'error'
                      }
                      reject(rest)
                    })
                  } else {
                    resolve({
                      status: 'cancel'
                    })
                  }
                }, 5000)
              }
            } else {
              resolve({
                status: 'cancel'
              })
            }
          }
          queryResult(orderNo)
        })
      ])
    }
  }
}
</script>

<style scoped lang="scss">
.pay-popup {
  padding: 20px 10px 20px 10px;
  .pay-name,
  .pay-no,
  .pay-type {
    line-height: 26px;
    padding-left: 15px;
  }
  .pay-desc {
    color: #409eff;
    text-align: center;
    padding: 5px 0;
    font-size: 18px;
    user-select: none;
    .pay-icon {
      width: 60px;
      margin-right: 10px;
    }
  }
  .pay-qr {
    display: flex;
    flex-direction: row;
    user-select: none;
    .pay-price {
      text-align: center;
      color: #ff6700;
      .price-unit {
        font-size: 32px;
      }
    }
    .pay-left {
      & > img {
        width: 200px;
        height: 200px;
      }
    }
    .pay-right {
      flex-grow: 1;
      padding-top: 15px;
    }
    .pay-msg {
      font-size: 14px;
      color: #000;
    }
    .pay-down {
      font-size: 40px;
      text-align: center;
      color: red;
    }
    .pay-oper {
      margin-top: 10px;
      text-align: center;
    }
  }
}
.pro-pays {
  text-align: center;
  margin: 15px 0;
}
.pro-btns {
  text-align: right;
}
.pro-qr {
  text-align: center;
  margin-bottom: 15px;
}
.pro-amoun {
  text-align: center;
  margin-top: 15px;
}
.pro-desc {
  padding: 5px 0;
  text-align: center;
}
.pro-prompt {
  padding-top: 40px;
  text-align: center;
  .pp-contact {
    display: inline-block;
    text-align: left;
  }
  .pp-btns {
    padding-top: 20px;
  }
}
</style>
