<template>
  <div v-if="selectComp" class="page-view">
    <div class="comp-tip">
      <div>
        <span class="comp-name">{{ selectComp.name }}</span>
        <template v-if="authComp">
          <span class="comp-type"> - {{ $utils.getCompType(authComp.authType) }}</span>
          <span class="comp-support">
            <span></span>
            <span v-if="loginInfo.currDate > authComp.supportExpiryDate" class="content red"></span>
            <span v-else class="content green">{{ authComp.supportExpiryDate }}</span>
          </span>
          <template v-if="authComp.isInvoice">
            <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.open()" content=""></vxe-button>
          </template>
          <template v-else-if="selectComp.invoice">
            <span v-if="$utils.isWithin30Day(authComp.authDate)" class="comp-invoice">
              <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.submit()" content=""></vxe-button>
            </span>
          </template>
        </template>
        <template v-else>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.codeType) }}</span>
          <span class="comp-amoun">{{ selectComp.amount }}</span>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
          <span class="comp-support" v-if="selectComp.supportDay >= 365"> 1 </span>
        </template>
      </div>
      <template v-if="loginInfo && authComp">
        <ul>
          <li><span class="green bold">{{ $utils.getCompExpiration(authComp.deadline) }}</span></li>
        </ul>
      </template>
      <template v-else>
        <ul>
          <li>{{ selectComp.codeType === 'src' ? '' : ''}}</li>
        </ul>
      </template>
      <div class="comp-agreement">
        <a class="link" @click="$refs.authAgreement.open()"></a>
      </div>
      <div class="comp-oper">
        <vxe-button class="btn" status="danger" @click="$refs.compOrder.open()" v-if="!authComp"></vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseLog.open()">{{ authComp ? ' v3.0 ' : ' v3.0 ' }}</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseNextLog.open()">{{ authComp ? ' v4.0 ' : ' v4.0 ' }}</vxe-button>
        <vxe-button class="btn" @click="$router.replace({name: backName})"></vxe-button>
      </div>
    </div>
    <div class="comp-preview">
      <div class="preview">
        <img :src="imgUrl">
      </div>
      <ul class="features">
        <li class="red"></li>
        <li v-for="(item, index) in selectComp.features" :key="index">{{ item }}</li>
      </ul>
    </div>
    <div class="comp-api">
      <a class="link" href="/vxe-table/#/table/api?q=pro" target="_black"> API</a>
    </div>
    <div class="comp-desc">
      <pre>
        <pre-code class="text">{{ apiDesc[0] }}</pre-code>
      </pre>
    </div>
    <pre>
      <pre-code class="html">{{ demoCodes[0] }}</pre-code>
      <pre-code class="javascript">{{ demoCodes[1] }}</pre-code>
    </pre>
    <release-log ref="releaseLog" :code="code"></release-log>
    <release-log ref="releaseNextLog" :code="code" :release-code="`${code}Next`"></release-log>
    <comp-order ref="compOrder" :code="code"></comp-order>
    <auth-agreement ref="authAgreement" :code="code"></auth-agreement>
    <invoice-info ref="invoiceInfo" :code="code"></invoice-info>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      code: 'PRO',
      backName: 'Pro',
      imgUrl: require('../../assets/image/plugin/proClip.gif'),
      apiDesc: [
        `
        node-sass

        
         Demo  plugins/*  *.es6.min.js  *.min.css 

        `
      ],
      demoCodes: [
        `
        <vxe-grid v-bind="gridOptions"></vxe-grid>
        `,
        `
        export default {
          data () {
            return {
              gridOptions: {
                border: true, // 
                resizable: true, // 
                autoResize: true, // 
                keepSource: true, // 
                showOverflow: true, // 
                height: 600, // 

                // 
                editConfig: {
                  trigger: 'dblclick', // 
                  mode: 'cell', // 
                  showUpdateStatus: true, // 
                  showInsertStatus: true // 
                },

                // 
                mouseConfig{
                  area: true, // 
                  extension: true //  Shift + 
                },

                //  mouse-config.area 
                areaConfig: {
                  selectCellByHeader: true, // 
                  extendByCopy: false, //  shift 
                  extendByCalc: false //  ctrl 
                },

                //  keyboard-config.isClip 
                clipConfig: {
                  isCopy: true, //  Ctrl + C 
                  isCut: true, //  Ctrl + X 
                  isPaste: true, //  Ctrl + V 
                  isRowIncrement: false, // 
                  isColumnIncrement: false // 
                },

                //  keyboard-config.isFNR 
                fnrConfig: {
                  isFind: false, //  Ctrl + F 
                  isReplace: false //  Ctrl + H 
                },

                // 
                keyboardConfig{
                  isClip: true, // Ctrl + X Ctrl + C Ctrl + V 
                  isMerge: true, // Ctrl + M 
                  isFNR: true, // Ctrl + F Ctrl + H 
                  isEdit: true, // Esc F2 
                  isTab: true, // Tab Shift + Tab 
                  isArrow: true, // 
                  isEnter: true, // Enter Shift + Enter 
                  isDel: true, // Delete Backspace 
                  isChecked: true, // Spacebar 
                  enterToTab: true //  Tab 
                },

                // 
                menuConfig: {
                  body: {
                    options: [
                      [
                        { code: 'COPY_CELL', name: ' (Ctrl+C)' }, //  Excel  WPS
                        { code: 'CUT_CELL', name: ' (Ctrl+X)' }, //  Excel  WPS
                        { code: 'PASTE_CELL', name: ' (Ctrl+V)' } //  Excel  WPS
                      ],
                      [
                        { code: 'OPEN_FIND', name: ' (Ctrl+F)' }, // 
                        { code: 'OPEN_REPLACE', name: ' (Ctrl+H)' }, // 
                        { code: 'MERGE_OR_CLEAR', name: '/ (Ctrl+M)' } // 
                      ],
                      [
                        { code: 'INSERT_AT_ROW', name: '' }, // 
                        { code: 'EDIT_CELL', name: '' }, // 
                        { code: 'DELETE_ROW', name: '' } // 
                      ],
                      [
                        {
                          name: '',
                          children: [
                            { code: 'CLEAR_SORT', name: '' }, // 
                            { code: 'SORT_ASC', name: '' }, // 
                            { code: 'SORT_DESC', name: '' } // 
                          ]
                        }
                      ],
                      [
                        {
                          name: '',
                          prefixIcon: 'fa fa-area-chart',
                          children: [
                             // 
                            { code: 'CHART_BAR_X_AXIS', name: ' - ' },
                            { code: 'CHART_BAR_X_AXIS', name: ' - ', params: { category: 'a' } },
                            { code: 'CHART_BAR_Y_AXIS', name: ' - ' },
                            { code: 'CHART_BAR_Y_AXIS', name: ' - ', params: { category: 'a' } },
                             // 
                            { code: 'CHART_LINE', name: ' - ' },
                            { code: 'CHART_LINE', name: ' - ', params: { category: 'a' } },
                             // 
                            { code: 'CHART_PIE', name: ' - ' },
                            { code: 'CHART_PIE', name: ' - ', params: { category: 'a' } }
                          ]
                        }
                      ]
                    ]
                },

                // 
                columns: [
                  { type: 'seq', width: 60 },
                  {
                    title: '1',
                    children: [
                      { field: 'a', title: 'A', align: 'right', width: 120, editRender: { name: '$input' } },
                      { field: 'b', title: 'B', width: 180, editRender: { name: '$input' } },
                      { field: 'c', title: 'C', width: 200, editRender: { name: '$input' } },
                      { field: 'd', title: 'D', width: 160, editRender: { name: '$input' } }
                    ]
                  },
                  {
                    title: '1',
                    children: [
                      { field: 'e', title: 'E', width: 100, editRender: { name: '$input' } },
                      { field: 'f', title: 'F', width: 140, editRender: { name: '$input' } },
                      { field: 'g', title: 'G', width: 160, editRender: { name: '$input' } },
                      { field: 'h', title: 'H', width: 120, editRender: { name: '$input' } }
                    ]
                  },
                  { field: 'i', title: 'I', width: 200, editRender: { name: '$input' } },
                  { field: 'j', title: 'J', width: 100, editRender: { name: '$input' } },
                  { field: 'k', title: 'K', width: 160, editRender: { name: '$input' } },
                  { field: 'l', title: 'L', width: 100, editRender: { name: '$input' } },
                  { field: 'm', title: 'M', width: 120, editRender: { name: '$input' } },
                  { field: 'n', title: 'N', width: 180, editRender: { name: '$input' } }
                ],

                // 
                data: [
                  { id: 10001, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' },
                  { id: 10002, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' },
                  { id: 10003, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' },
                  { id: 10004, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' }
                ]
              }
            }
          }
        }
        `
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  }
}
</script>
