<template>
  <div v-if="selectComp && authComp">
    <vxe-modal v-model="expressVisible" title="" width="600" height="460" show-zoom resize esc-closable>
      <template v-if="authComp.isInvoice && applyFormData.expressName">
        <div><span class="green"></span></div>
        <div><span class="bold">{{ applyFormData.expressName }} {{ applyFormData.expressNo }}</span></div>
      </template>
      <template v-else>
        <div><span class="primary"></span></div>
      </template>
      <div></div>
      <ul>
        <li>{{ applyFormData.expressBy }}</li>
        <li>{{ applyFormData.expressPhone }}</li>
        <li>{{ applyFormData.expressAddress }}</li>
      </ul>
      <div></div>
      <ul>
        <li>{{ applyFormData.name }}</li>
        <li>{{ applyFormData.dutyNo }}</li>
        <li>{{ applyFormData.address }}</li>
        <li>{{ applyFormData.phone }}</li>
        <li>{{ applyFormData.bankName }}</li>
        <li>{{ applyFormData.bankOutlets }}</li>
        <li>{{ applyFormData.bankAccount }}</li>
      </ul>
      <div style="text-align: right;"> <qq-service></qq-service> </div>
    </vxe-modal>

    <vxe-modal v-model="applyVisible" :loading="applyLoading" title="" width="700" height="780" show-zoom resize>
      <vxe-form ref="xForm" :data="applyFormData" :rules="isEnterprise ? gsFormRules : grFormRules" title-width="100" title-align="right" @submit="submitApply">
        <vxe-form-item span="24" align="center" v-if="!loginInfo || !loginInfo.aType">
          <template #default>
            <vxe-radio-group v-model="applyFormData.userType" @change="typeChangeEvent">
              <vxe-radio label="1" content=""></vxe-radio>
              <vxe-radio label="3" content=""></vxe-radio>
            </vxe-radio-group>
          </template>
        </vxe-form-item>

        <vxe-form-item title="" span="24" :title-colon="false" :title-suffix="{message: ''}"></vxe-form-item>
        <vxe-form-item field="expressBy" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}"></vxe-form-item>
        <vxe-form-item field="expressPhone" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}"></vxe-form-item>
        <vxe-form-item field="expressAddress" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}"></vxe-form-item>

        <vxe-form-item title="" span="24" :title-colon="false" :title-suffix="{message: ''}"></vxe-form-item>
        <vxe-form-item field="name" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: isEnterprise ? '' : ''}}"></vxe-form-item>
        <vxe-form-item field="dutyNo" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}"></vxe-form-item>
        <vxe-form-item field="address" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}" :visible-method="visibleMethod"></vxe-form-item>
        <vxe-form-item field="phone" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}" :visible-method="visibleMethod"></vxe-form-item>
        <vxe-form-item field="bankName" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}" :visible-method="visibleMethod"></vxe-form-item>
        <vxe-form-item field="bankOutlets" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}" :visible-method="visibleMethod"></vxe-form-item>
        <vxe-form-item field="bankAccount" title="" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: ''}}" :visible-method="visibleMethod"></vxe-form-item>

        <vxe-form-item align="center" span="24">
          <template #default>
            <vxe-button type="submit" content="" status="primary"></vxe-button>
            <vxe-button type="reset" content=""></vxe-button>
          </template>
        </vxe-form-item>
      </vxe-form>
    </vxe-modal>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'

export default {
  name: 'InvoiceInfo',
  props: {
    code: String
  },
  data () {
    return {
      expressVisible: false,
      applyVisible: false,
      applyLoading: false,
      applyFormData: {
        userType: '1',
        name: '',
        dutyNo: '',
        address: '',
        phone: '',
        bankName: '',
        bankOutlets: '',
        bankAccount: '',

        expressName: '',
        expressNo: '',
        expressBy: '',
        expressPhone: '',
        expressAddress: ''
      },
      gsFormRules: {
        name: [
          { required: true, message: '' }
        ],
        dutyNo: [
          { required: true, message: '' }
        ],
        expressBy: [
          { required: true, message: '' }
        ],
        expressPhone: [
          { required: true, message: '' }
        ],
        expressAddress: [
          { required: true, message: '' }
        ]
      },
      grFormRules: {
        name: [
          { required: true, message: '' }
        ],
        expressBy: [
          { required: true, message: '' }
        ],
        expressPhone: [
          { required: true, message: '' }
        ],
        expressAddress: [
          { required: true, message: '' }
        ]
      }
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    isEnterprise () {
      return this.applyFormData.userType === '1'
    },
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  methods: {
    ...mapActions([
      'updateUser'
    ]),
    visibleMethod () {
      return this.isEnterprise
    },
    open () {
      this.expressVisible = true
      XEAjax.get(`/api/pub/vxetable/invoice/${this.code}`).then(data => {
        const rest = this.$utils.getRest(data)
        XEUtils.destructuring(this.applyFormData, rest ? rest.result : {})
        this.updateUser()
      })
    },
    submit () {
      this.applyVisible = true
      XEAjax.get(`/api/pub/vxetable/invoice/${this.code}`).then(data => {
        const rest = this.$utils.getRest(data)
        XEUtils.clear(this.applyFormData, '', { userType: '1' })
        XEUtils.destructuring(this.applyFormData, rest ? rest.result : {})
        this.updateUser()
      }).catch(() => {
        XEUtils.clear(this.applyFormData, '', { userType: '1' })
      })
    },
    submitApply () {
      this.applyLoading = true
      XEAjax.post(`/api/pub/vxetable/invoice/apply/${this.code}`, this.applyFormData).then(data => {
        const rest = this.$utils.getRest(data)
        this.applyLoading = false
        if (rest && rest.code === 0) {
          this.applyVisible = false
          this.$XModal.message({ message: rest.message, status: 'success' })
        } else {
          this.$XModal.message({ message: rest ? rest.message : '', status: 'error' })
        }
      }).catch((data) => {
        const rest = this.$utils.getRest(data)
        this.applyLoading = false
        this.$XModal.alert({ message: rest ? rest.message : '', status: 'error' })
      }).then(() => {
        this.updateUser()
      })
    },
    typeChangeEvent () {
      this.$refs.xForm.reset()
    }
  }
}
</script>
